<template >
    <div class="greyDetails hide">
        <div class="container">
            <div class="row">
                <div class="col-md-12">
                    <img :src = 'CaseDetailImg' alt=""/>
                </div>
                <!-- <div class="col-md-2">
                    <h3>{{CaseDetailTT}}</h3>
                    <p>{{CaseDetailP}}</p>
                </div> -->
            </div>
        </div>
        <div class="scroll">
            <img src="../../static/img/scroll.png" alt="">
        </div>
        <!-- <h1>{{CaseDetailId}}</h1> -->
    </div>
</template>

<script>
export default {

    name:'greyDetails',
    props:['CaseDetailId','CaseDetailImg','CaseDetailTT','CaseDetailP']

}
$(function(){
    $(".scroll").on("click",function(){
        $(this).parent().hide();
        $("body").css("overflow","scroll");
        // $("body").style.overflow="scroll";
        // console.log(this.logo)
    })
})
</script>

<style>
.greyDetails{position: absolute;top:0;left: 0;width: 100vw;height: 100vh;background-color: rgba(0,0,0,0.6);z-index: 199;padding: 100px 0;color: #fff;overflow: scroll;text-align: left;}
body{width: 100%;height: 100%;/* overflow: hidden; */}
.greyDetails img{width: 100%;}
.greyDetails h3{font-size: 1rem;}
.greyDetails p{font-size: .7rem;}
.greyDetails .scroll{position: absolute;width: 30px;height: 30px;top: 40px;right: 50px;}
</style>



