<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <!-- <router-view/> -->
    <Head></Head>
    <div class="index_video">
        <video class="shouye-video" :src="this.bannerUrl" autoplay="autoplay" loop="loop">
            您的浏览器不支持 video 标签。
        </video> <!-- banner 视频 -->
    </div>
    <div class=" shouye-bt">
        <h3>品牌案例</h3>
        <p>
            <span>BRAND CASE</span>
        </p>
    </div>

    <div class="projectList">
        <div class="ul container">
            <ul class="row projectList-con">
                <li v-for="projectList in projectLists" page-data="1" class="col-md-4 col-ms-12 2" v-on:click="CaseDetailF($event)">
                    <div class="con-shadow-before projectList-list-con projectList-list-con1" v-bind:data-id="projectList.id">
                        <img class="" :src = 'projectList.brand_logo'/>
                        <p class="projectList-list-con-txt">{{projectList.description}}</p>
                    </div>
                </li>
                <!-- <li v-for="addMoreList in addMoreLists" :page-data="pagaData" class="col-md-4 col-ms-12 2 projectListMore" v-on:click="CaseDetailF($event)">
                    <div class="con-shadow-before projectList-list-con" v-bind:data-id="addMoreList.id">
                        <img class="" :src = 'addMoreList.brand_logo'/>
                        <p class="projectList-list-con-txt">{{addMoreList.description}}</p>
                    </div>
                </li> -->
            </ul>
            <div class="projectMore">
                <a href="javascript:;">
                    <img src="static/img/more.png" alt=""  v-on:click="addMoreF()">
                </a>
                <p>Load More</p>
                <!-- v-on:click ="addMoreF()" -->
            </div>
        </div>
    </div> <!-- 品牌案例 -->

    <div class="ledaoImg">
        <img src="static/img/map.gif" alt="">
        <p>至今我们的业务覆盖了23省</p>
    </div> <!-- GIF图 -->
    <div class="serviceList">
        <div class="serviceList-title" >
            <h3>服务品牌</h3>
            <p>
                <span>SERVICE BRAND</span>
            </p>
        </div>

        <div class="serviceList-con">
            <div class="container">
                <div class="row">
                    <div class="col-md-12">
                        <img src="static/img/serviceList.jpg" alt="">
                    </div>
                </div>
            </div>
        </div>
    </div> <!-- 服务品牌 -->
    <bottomBar></bottomBar>
    <canvasBg></canvasBg>
    <greyDetails :CaseDetailId="CaseDetailId"  :CaseDetailImg="CaseDetailImg" :CaseDetailTT="CaseDetailTT" :CaseDetailP="CaseDetailP"></greyDetails>

</div>
</template>

<script>
import bottomBar from './components/bottomBar'
import canvasBg from './components/canvasBg'
import Head from './components/Head'
import greyDetails from './components/greyDetails'

export default {
    name: 'App',
    components: {
        bottomBar,
        canvasBg,
        Head,
        greyDetails
    },
    data:function(){
        return{
            indexList:'',
            bannerUrl:'',
            projectLists:'',
            serviceLists:'',
            addMoreLists:'',
            logomsg:'——来自父组件',
            CaseDetailId:'',
            CaseDetailImg:'',
            CaseDetailTT:'',
            CaseDetailP:'',
            pagaData:1,
            maxPage:''
        }
    },
    created(){
           this.videoF();
    },
    methods:{
        videoF:function(){ //video + 品牌案例
            console.log("load");
            $(".con-shadow-before").mouseover(function() {
                $(this).addClass("con-shadow-after")
            });
            $(".con-shadow-before").mouseout(function() {
                $(this).removeClass("con-shadow-after")
            });

            $(".projectList-list-con").mouseover(function() {
                $(this).find(".projectList-list-con-txt").fadeIn(500);
            });
            $(".projectList-list-con").mouseout(function() {
                $(".projectList-list-con-txt").hide();
            });

            this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/index',{
                type: "sydata"
                }, {
                headers: {
                    "X-Requested-With": "XMLHttpRequest"
                },
                emulateJSON: true
            }).then(function (data) {
                self.b = data;

                this.bannerUrl=data.body.data.banner[0].bannerUrl;
                this.indexList=data.body.data;
                this.projectLists=data.body.data.brand_case;
                this.serviceLists=data.body.data.service_brand;
                this.pagadata=data.body.data.currentPage;
                // console.log(this.pagadata)

                $(".con-shadow-before").mouseover(function() {
                    $(this).addClass("con-shadow-after")
                });
                $(".con-shadow-before").mouseout(function() {
                    $(this).removeClass("con-shadow-after")
                });

                $(".projectList-list-con").mouseover(function() {
                    $(this).find(".projectList-list-con-txt").fadeIn(500);
                });
                $(".projectList-list-con").mouseout(function() {
                    $(".projectList-list-con-txt").hide();
                });
            })
        },
        addMoreF:function(){ //更多加载
            // $(".projectListMore").show();
            // get传参数 需要 {params: {你传的值}}
            this.pagaData++;
            var addMoreList;
            var projectListHtml='';
            this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/loadMore',{params: {currentPage:this.pagaData}})
            .then( (res) => {
                 this.addMoreLists=res.data.data.brand_case;
                 this.maxPage = res.data.data.maxPage;
                 // cons
                 if (this.pagaData > this.maxPage){
                     alert('没有更多了哦')
                    }else{
                        var i;
                        for(i in this.addMoreLists){
                            projectListHtml +='<li page-data='+this.pagaData+' class="col-md-4 col-ms-12 2">'
                            projectListHtml +='<div class="con-shadow-before projectList-list-con new-Plst" data-id='+this.addMoreLists[i].id+'>'
                            projectListHtml +='<img class="" src = '+this.addMoreLists[i].brand_logo+'>'
                            projectListHtml +='<p class="projectList-list-con-txt">'+this.addMoreLists[i].description+'</p>'
                            projectListHtml +='</div>'
                            projectListHtml +='</li>'
                        }
                         // console.log(this.methods.CaseDetailF());
                        $(".projectList-con").append(projectListHtml);
                        var that = this;
                         $(".new-Plst").on("click",function(){
                             var dataid2 = $(this).attr('data-id');
                             // console.log("dataid2",dataid2)

                             $(".greyDetails").fadeIn(1400);
                             $("body").css("overflow","hidden");
                             $("html,body").animate({
                                    scrollTop:0
                                }, 1000)
                                that.$http.get('http://adwww.ldcywh.com/index.php/index/Api/getCaseDetail',{params: {id:dataid2}})
                                .then( (res) => {
                                    // console.log(res.data)
                                    that.CaseDetailImg = res.data.data.brand_case.detail_pic;
                                    that.CaseDetailTT = res.data.data.brand_case.title;
                                    that.CaseDetailP = res.data.data.brand_case.description;
                                    // console.log(that.CaseDetailImg)
                                    // console.log(that.CaseDetailTT)
                                    // console.log(that.CaseDetailP)
                                })
                         });
                     }
                 $(".con-shadow-before").mouseover(function() {
                     $(this).addClass("con-shadow-after")
                 });
                 $(".con-shadow-before").mouseout(function() {
                     $(this).removeClass("con-shadow-after")
                 });

                 $(".projectList-list-con").mouseover(function() {
                     $(this).find(".projectList-list-con-txt").fadeIn(300);
                 });
                 $(".projectList-list-con").mouseout(function() {
                     $(".projectList-list-con-txt").hide();
                 });
                 $(".projectList-list-con").on("click",function() {
                     // console.log("click")
                 });
            })
        },
        CaseDetailF:function(obj){ //案例详情
            $(".projectList-list-con1").on("click",function(){
                $(".greyDetails").fadeIn(1400);
                $("body").css("overflow","hidden");
                $("html,body").animate({
                       scrollTop:0
                   }, 1000)
            })
            var data = $(event.currentTarget);
            var dataid = data.find("div").attr('data-id');
            // console.log(dataid)
            this.CaseDetailId = dataid;
            this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/getCaseDetail',{params: {id:dataid}})
            .then( (res) => {
                console.log(res.data)
                this.CaseDetailImg = res.data.data.brand_case.detail_pic;
                this.CaseDetailTT = res.data.data.brand_case.title;
                this.CaseDetailP = res.data.data.brand_case.description;
                // console.log(this.CaseDetailP)

            })
        }


    }

}
$(function(){

    // $(".projectList-list-con").on("click",function(){
    //     $(".greyDetails").show();
    //     $("body").css("overflow","hidden");
    //     $("html,body").animate({
    //            scrollTop:0
    //        }, 500)
    // })
    setTimeout(function(){
        $(".con-shadow-before").mouseover(function() {
            $(this).addClass("con-shadow-after")
        });
        $(".con-shadow-before").mouseout(function() {
            $(this).removeClass("con-shadow-after")
        });

        $(".projectList-list-con").mouseover(function() {
            $(this).find(".projectList-list-con-txt").fadeIn(500);
        });
        $(".projectList-list-con").mouseout(function() {
            $(".projectList-list-con-txt").hide();
        });
    },500)


    $(".con-shadow-before").mouseover(function() {
        // console.log("over")
        $(this).addClass("con-shadow-after")
    });
    $(".con-shadow-before").mouseout(function() {
        $(this).removeClass("con-shadow-after")
    });
    $(".projectList-list-con").on("click",function(){
        var data = $(this).attr('data-id');
        console.log(data)
    })

})
</script>

<style>
    body,p,h1,h2,h3,h4,span{font-family: "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";}
    html{font-family: "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";}
    .main-ym{color: #fff !important;}
    li {list-style-type:none;}
    .index_video{background: url(http://www.ldcywh.com/static/img/index_video.jpg) no-repeat;background-size: 100% 100%}
    .shouye-bt{padding: 40px 0;}
    .shouye-bt p{margin-top: 0;margin-bottom: 1rem;font-size: 1.1rem !important;color: #ccc !important;text-align: center;letter-spacing: 13px;border-top: 1px solid #ccc;line-height: 38px;display: inline-block;text-align: center;padding-left: 13px;font-weight: 500;}
    .projectList{margin: 0 30px;}
    .projectList-list-con{position: relative;}
    .projectList-list-con-txt{display: none;position: absolute;bottom: 0;width: 100%;margin: 0;padding: 0;line-height: 30px;text-align: left;padding-left: 20px;background-color: rgba(0,0,0,0.2);color: #fff;}
    .projectList ul{width: 100%;}
    .projectList li{padding: 5px !important;}
    .projectList img{width: 100%;}
    .projectMore{margin-bottom: 50px;margin-top: 30px;}
    .projectMore img{width: 50px;}
    .projectMore p{color: #000 !important;margin-top: 30px !important;font-size: 22px;letter-spacing: 8px;}
    .projectListMore{display: none;}

    /* .serviceList-title{border-bottom: 1px solid #62c4d1;} */
    .serviceList-title p{margin: 0;}
    .serviceList-title h3{padding-top: 60px;color: #3f3a39;font-weight: 600;text-align: center;font-size: 1.3rem;letter-spacing: 2px;}
    .serviceList-title span{margin-top: 0;margin-bottom: 1rem;font-size: 1.1rem;color: #ccc;text-align: center;letter-spacing: 13px;border-top: 1px solid #ccc;line-height: 38px;display: inline-block;text-align: center;padding-left: 13px;}
    .serviceList-con{/* height: 300px; */padding:50px 0;}
    .serviceList-con .col-md-3{font-family: "SF Pro SC","SF Pro Text","SF Pro Icons","PingFang SC","Helvetica Neue","Helvetica","Arial",sans-serif;line-height: 60px;font-size: 16px;text-align: left;}
    .serviceList-con img{width: 100%;}
    .serviceList-con .col-md-12{padding: 0 10%;}
    .hide{display: none;}
    .main-color{color: #62c4d1 !important;}
    #app {font-family: "SF Pro SC","SF Pro Text","SF Pro Icons","PingFang SC","Helvetica Neue","Helvetica","Arial",sans-serif;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;text-align: center;color: #2c3e50;margin-top: 0px;}
    .shouye-video{width: 100%;margin-top: 35px;padding: 5% 16%;}
    .ledaoImg{width: 100%;background-color: #f5f5f5;}
    .ledaoImg p{line-height: 70px;margin: 0;background-color: #ececec;font-size: 20px;color: #000;letter-spacing: 6px;}
    .ledaoImg img{width: 100%;}
    .con-shadow-after{
    box-shadow:0 0 20px #d9d9d9;
    transform: translateY(-5px);
    -webkit-animation: danyuan_men_point .2s linear alternate forwards;
    -o-animation: danyuan_men_point .2s linear alternate forwards;
    animation: danyuan_men_point .2s linear alternate forwards;
    }
    @-webkit-keyframes danyuan_men_point {
        0% { transform:translateY(0px);}
        100%{ transform:translateY(-5px);}
    }
    @-o-keyframes danyuan_men_point {
        0% { transform:translateY(0px);}
        100%{ transform:translateY(-5px);}
    }
    @keyframes danyuan_men_point {
        0% { transform:translateY(0px);}
        100%{ transform:translateY(-5px);}
    }
</style>



